import { isObject, urlencode } from './util'
import { isApp } from './env'


// Á±ªÂûãÊ£ÄÊü•
function check (vms, _vmName, _data) {
  if (_vmName && !vms.hasOwnProperty(_vmName)) { throw new Error('Ê≤°ÊúâÂèëÁé∞' + _vmName) }
  if (_data && !isObject(_data)) { throw new Error(_vmName + ': ‰º†ÂÖ•ÁöÑ$dataÂ∫î‰∏∫‰∏Ä‰∏™ÂØπË±°') }
}

export default function (g) {
  if (!isApp) { return }
  if (g.hasOwnProperty('bridge')) {
    throw new Error('bridgeÂ≠óÊÆµË¢´Âç†Áî®')
  } else {
    if (!g.vms) { throw new Error('Ê≤°ÊúâÂèëÁé∞vmsÂ≠óÊÆµ') }
    let vms = g.vms
    if (!isObject(vms)) { throw new Error('vmsÁ±ªÂûãÈîôËØØ') }

    g.bridge = {
      // Êü•Áúãdata
      getData (_vmName, isStringify) {
        _vmName = String(_vmName)
        check(vms, _vmName)
        let vm = vms[_vmName]
        return isStringify ? JSON.stringify(vm.$data, null, 4)
          : Object.assign({}, vm.$data)
      },
      // Êé®ÈÄÅÊï∞ÊçÆÂà∞ÁªÑ‰ª∂
      pushData (_vmName, _data) {
        _vmName = String(_vmName)
        check(vms, _vmName, _data)
        let vm = vms[_vmName]
        Object.assign(vm, _data)
      }





    }
  }
}


function execute (url) {
  let iframe = document.createElement('IFRAME')
  iframe.setAttribute('src', url)
  document.documentElement.appendChild(iframe)
  iframe.parentNode.removeChild(iframe)
  iframe = null
}

