import Vue from 'vue'
import App from '@/App'
import Router from 'vue-router'
import 'html5-reset/assets/css/reset.css'

import liveNormalDetail from '@/view/liveStreaming/live-normal-detail.vue'; //ÂõæÊñáÁõ¥Êí≠È°µ

import moment from 'moment'
Vue.filter('dateFormat', function (value, type) { //Êó∂Èó¥Ê†ºÂºèÂåñËøáÊª§
  return moment(value).format(type);
});

Vue.use(Router)

const router = new Router({
  routes: [{
    path: '/',
    name: 'liveNormalDetail',
    component: liveNormalDetail
  }]
})


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



