<template>
   <div>
      <ul class="pushList">
         <li class="box" v-for="(item,index) in resData " :key="index" @click.stop="handleOut(item.id)">
              <!-- Â§¥ÈÉ® start -->
              <div class="editorInfo">
                  <img :src="item.user_profile.avatar_path">
                  <p>{{item.user_profile.username}}</p>
                  <!-- <p>{{ window.dg_unit.getDateDiff(item.user_profile.created_at) }}</p> -->
                  <p>{{ item.user_profile.created_at | getDateDiff }}</p>
              </div>
              <!-- Â§¥ÈÉ® end -->

              <!-- ÊñáÊú¨ start -->
              <div class="text">
                  {{item.content}}
              </div>
              <!-- ÊñáÊú¨ end -->
              
              <!-- Â§öÂõæswiper start -->
              <div v-if="item.imgs_url.length>1" class="swiperBox">
                  <swiper :options="swiperOption" ref="mySwiper">
                     <!-- slides -->
                     <swiper-slide class="swiper" v-for="(items,indexs) in item.imgs_url " :key="indexs"><img :src="items" @click.stop="handleViewImg(items)"></swiper-slide>
                     <!-- Optional controls -->
                     <div class="swiper-pagination" slot="pagination"></div>
                  </swiper>
                  <!-- Âè≥‰∏äËßíËÆ°Êï∞ -->
                  <div class="nowPagination">
                      {{paginationNo + 1}}  / {{item.imgs_url.length}}
                  </div>
              </div>
              <!-- Â§öÂõæswiper end -->

              <!-- ÂçïÂõæ start -->
              <div v-else-if="item.imgs_url.length==1" class="singleImage">
                 <img :src="item.imgs_url[0]" @click.stop="handleViewImg(item.imgs_url[0])">
              </div>
              <!-- ÂçïÂõæ end -->

              <!-- ÂõûÂ§çÊÄªÊï∞ start -->
              <div class="totalComment" v-if="item.reply_count">
                  <span class='icon-in_comments_border_icon'></span>
                  <span>{{item.reply_count}}</span>
              </div>
              <!-- ÂõûÂ§çÊÄªÊï∞ end -->

              <!-- ÂõûÂ§çÂå∫Âüü start -->
              <div class="comment" @click.stop="handleInner(item.id)" v-if="item.comments.length>0">
                  <!-- ËØ¶ÁªÜËØÑËÆ∫ -->
                  <ol class="commentList">
                      <li v-for="cms in  item.comments" :key="cms.id">

                         <div v-if="cms.parent_id==0">
                            <span class="user">{{cms.user_profile.username}}</span> : <span class="content">{{cms.content}} 
                                <i v-if="cms.imgs_url.length>0" ><span class='commentImgDetail' @click.stop='commentImgDetail(cms.imgs_url[0] ? cms.imgs_url[0]: "")'>ÂõæÁâáËØ¶ÊÉÖ</span></i>
                            </span>
                         </div>

                         <div v-else>
                            <span class="user">{{cms.parent?cms.parent.user_profile.username:''}}</span> ÂõûÂ§ç <span class="user">{{cms.user_profile.username}}</span> : <span class="content">{{cms.parent?cms.parent.content:''}}</span>
                         </div>
          
                      </li>
                  </ol>
                  <!-- total -->
                  <p v-if="item.reply_count" class="totalComments">ÂÖ± {{item.reply_count}} Êù°ËØÑËÆ∫ > </p>
              </div>
              <!-- ÂõûÂ§çÂå∫Âüü end -->
         </li>
      </ul>
      
   </div>
</template>
<script>
// http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg
// http://img0.imgtn.bdimg.com/it/u=4005596794,992112216&fm=27&gp=0.jpg
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  props: ["resData","sort"],
  components: { swiper, swiperSlide },
  data() {
    return {
      swiperOption: {
        pagination: {
          el: ".swiper-pagination"
        },
        on: {
          slideChangeTransitionEnd: function(event) {
            let childArr = window.vms["live-normal-detail"]["$children"]
            childArr.forEach(element => {
                if(element['$options']['_componentTag'] =='pushEditor'){
                  element["paginationNo"] = this.activeIndex;
                }
            });
            // window.vms["live-normal-detail"]["$children"][1]["paginationNo"] =
          }
        }
      },
      paginationNo: 0,
      recordThisComm: null,
      isSort: false,
    };
  },
  methods: {
    handleOut() {
      console.log("out");
    },
    handleInner() {
      console.log("inner");
    },
    handleImgs() {
      console.log("imgs");
    },
    handleViewImg(url) {
      console.log("ÁºñËæëÊé®ÈÄÅÁöÑÂõæÁâá");
    },
    commentImgDetail(url) {
      console.log("ËØÑËÆ∫ÂõæÁâáËØ¶ÊÉÖ");
    }
  },
  watch: {
    sort: function(newVal, oldVal) {
      if (!newVal) {
        this.isSort = !this.isSort;
        this.resData.reverse();
      } else {
        if (!this.isSort) return;
        this.resData.reverse();
      }
    }
  },
  computed: {},
  filters: {
    getDateDiff: function(dateTimeStamp) {
      var result = "";
      var minute = 1000 * 60;
      var hour = minute * 60;
      var day = hour * 24;
      var halfamonth = day * 15;
      var month = day * 30;
      var now = new Date().getTime();
      var diffValue = now - dateTimeStamp;
      if (diffValue < 0) {
        return;
      }
      var monthC = diffValue / month;
      var weekC = diffValue / (7 * day);
      var dayC = diffValue / day;
      var hourC = diffValue / hour;
      var minC = diffValue / minute;
      if (monthC >= 1) {
        result = "" + parseInt(monthC) + "ÊúàÂâç";
      } else if (weekC >= 1) {
        result = "" + parseInt(weekC) + "Âë®Ââç";
      } else if (dayC >= 1) {
        result = "" + parseInt(dayC) + "Â§©Ââç";
      } else if (hourC >= 1) {
        result = "" + parseInt(hourC) + "Â∞èÊó∂Ââç";
      } else if (minC >= 1) {
        result = "" + parseInt(minC) + "ÂàÜÈíüÂâç";
      } else result = "ÂàöÂàö";
      return result;
    }
  },
  mounted() {},
  created() {}
};
</script>
<style lang='less'>
@m: 750/375;
.swiper-pagination-bullet {
  width: 6px * @m !important;
  height: 6px * @m !important;
  background-color: #fff !important;
}
</style>

<style lang='less' scoped>
@m: 750/375;
.pushList {
  position: relative;
  width: 100%;
}
.pushList .box {
  position: relative;
  width: 100%;
  overflow: hidden;
  background: #ffffff;
  box-shadow: 0 0 30px * @m 0 rgba(227, 227, 227, 0.5);
  border-radius: 6px * @m;
  box-sizing: border-box;
  padding: 16px * @m;

  margin-bottom: 40px;
}

.box {
  position: relative;
  width: 100%;
  overflow: hidden;
  background: #ffffff;
  box-shadow: 0 0 30px * @m 0 rgba(227, 227, 227, 0.5);
  border-radius: 6px * @m;
  box-sizing: border-box;
  padding: 16px * @m;

  margin-bottom: 40px;
}

// Â§¥ÈÉ®
.editorInfo {
  display: flex;
  justify-content: flex-start;
  flex-wrap: nowrap;
  align-items: center;
  margin-bottom: 14px * @m;
}
.editorInfo img {
  display: block;
  width: 40px * @m;
  height: 40px * @m;
  border-radius: 100%;
  margin-right: 10px * @m;
}
.editorInfo p:nth-of-type(1) {
  font-size: 14px * @m;
  color: #435771;
  margin-right: 6px * @m;
}
.editorInfo p:nth-of-type(2) {
  font-size: 12px * @m;
  color: #c6c6c6;
}
// ÊñáÊú¨
.text {
  font-size: 16px * @m;
  color: #0f2540;
  margin-bottom: 14px * @m;
}
// Â§öÂõæswiper
.swiperBox {
  position: relative;
  margin-bottom: 14px * @m;
}
.swiper img {
  width: 100%;
  height: 200px * @m;
  border-radius: 8px * @m;
}
.nowPagination {
  position: absolute;
  font-size: 12px * @m;
  color: #fff;
  top: 13px * @m;
  right: 18px * @m;
  z-index: 10;
}

// ÂçïÂõæ
.singleImage img {
  display: block;
  width: 163px * @m;
  height: 200px * @m;
  border-radius: 8px * @m;
  margin-bottom: 14px * @m;
}

// ÂõûÂ§çÊï∞
.totalComment {
  color: #c9d6de;
  display: flex;
  justify-content: flex-start;
  flex-wrap: nowrap;
  align-items: center;
  margin-bottom: 14px * @m;
}
.totalComment span:first-child {
  width: 20px * @m;
  height: 16px * @m;
}
.totalComment span:last-child {
  font-size: 12px * @m;
}

// ÂõûÂ§çÂå∫Âüü
.comment {
  width: 100%;
  color: #f3f3f3;
  box-sizing: border-box;
  padding: 10px * @m;
  border-radius: 6px * @m;
  background-color: #f3f3f3;
  color: #0f2540;
}
.comment .commentList li {
  margin-bottom: 10px * @m;
}
.comment .commentList li .user {
  font-size: 14px * @m;
  color: #649be1;
}
.comment .commentList li .content {
  font-size: 14px * @m;
  color: #0f2540;
}
.comment .totalComments {
  color: #649be1;
}
.commentImgDetail {
  font-size: 14px * @m;
  color: #649be1;
}
</style>


